import { createApp } from 'vue';
import App from './App.vue';
import store from './store';
import router from './router';
import 'vant/lib/index.css';

import {Popup, Area, AddressEdit, AddressList, Icon, SubmitBar, Checkbox, CheckboxGroup, SwipeCell, Stepper, Field, Form, Tag, Button, Image as VanImage, PullRefresh, List, Card, Tab, Tabs, Swipe, SwipeItem, Lazyload, Badge, Sidebar, SidebarItem, Collapse, CollapseItem, } from 'vant';

createApp(App)
    .use(Swipe).use(SwipeItem).use(Lazyload, {
        loading:require('./assets/img/default.jpg')
        }).use(Badge).use(Sidebar).use(SidebarItem).use(Collapse).use(CollapseItem).use(Form).use(Field)
        .use(Tab).use(Tabs).use(Card).use(PullRefresh).use(List).use(VanImage).use(Tag).use(Button)
        .use(Checkbox).use(CheckboxGroup).use(SwipeCell).use(Stepper).use(SubmitBar).use(Icon).use(AddressList)
        .use(AddressEdit).use(Area).use(Popup)
    .use(store).use(router).mount('#app')
    
