import { createRouter, createWebHistory } from 'vue-router'
import store from '../store';
import { AddressEdit, Notify, Toast} from 'vant';


const Home = () => import('../views/home/Home.vue');
const Category = () => import('../views/category/Category.vue');
const Detail = () => import('../views/detail/Detail.vue');
const Profile = () => import('../views/profile/Profile.vue');
const ShopCart = () => import('../views/shopcart/ShopCart.vue');
const Register = () => import('../views/profile/Register.vue')
const Login = () => import('../views/profile/Login.vue')

const routes = [
  {
    path: '',
    name: 'HomePage',
    component: Home,
    meta: {
      title:'图书商城'
    }
  },
  {
    path: '/home',
    name: 'Home',
    component: Home,
    meta: {
      title:'图书商城'
    }
  },
  {
    path: '/category',
    name: 'Category',
    component: Category,
    meta: {
      title:'图书商城-商品分类'
    }
  },
  {
    path: '/detail',
    name: 'Detail',
    component: Detail,
    meta: {
      title:'图书商城-商品详情'
    }
  },
  {
    path: '/profile',
    name: 'Profile',
    component: Profile,
    meta: {
      title:'图书商城-个人中心',
      isAuthRequired: true
    }
  },
  {
    path: '/shopcart',
    name: 'ShopCart',
    component: ShopCart,
    meta: {
      title:'图书商城-购物车',
      isAuthRequired: true
    }
  },
  {
    path: '/addresslist',
    name: 'AddressList',
    component: () => import('../views/profile/AddressList.vue'),
    meta: {
      title:'图书商城-地址管理',
      isAuthRequired: true
    }
  },
  {
    path: '/addressedit',
    name: 'AddressEdit',
    component: () => import('../views/profile/AddressEdit.vue'),
    meta: {
      title:'图书商城-编辑地址',
      isAuthRequired:true
    }
  },
  {
    path: '/register',
    name: 'Register',
    component: Register,
    meta: {
      title:'图书商城-用户注册'
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    meta: {
      title:'图书商城-用户登录'
    }
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to, from, next) => {
  // 如果没有登录，在这里到login
  if(to.meta.isAuthRequired && store.state.user.isLogin === false) {
    Notify('您还没有登录，请先登录');
      return next('/login');
  }else{
    next();
  }

  document.title = to.meta.title;
})

export default router
